import React, { useState } from 'react'
import Expand from 'react-expand-animated';

const Accordion = (props) => {
  const { children, summary, className, subClassname, open } = props
  const [expand, setExpand] = useState(open)

  return (
    <div className={`${className} px-4 items-center `}>
      <div className={`flex justify-between items-center ${subClassname}`} onClick={() => { if (!open) setExpand(!expand) }}>
        <div className='faq-subject text-white py-2 px-4 w-full cursor-pointer items-center justify-between flex'>
          <p>{summary}</p>
          {<img src="/icons/ic_next.png" alt='expand' className={` transform ${expand ? "rotate-90" : "rotate-0"} duration-500 h-4 flex-shrink-0`} />}
        </div>
      </div>
      <Expand open={expand} className='rounded-b-3xl border-b-4 border-l-4 border-r-4 border-app-purple font-gothic-italic font-bold'>
        <p className='px-4 py-2 '>
          {children}
        </p>
      </Expand>
    </div>
  )
}

Accordion.defaultProps = {
  open: false
}

export default Accordion